"use client";

import { useState } from "react";
import { ChevronDown, ChevronUp, Loader2, LucideIcon } from "lucide-react";

interface AgentCardProps {
  name: string;
  icon: LucideIcon;
  preview: string;
  content: string;
  status: "pending" | "running" | "done";
}

export function AgentCard({ name, icon: Icon, preview, content, status }: AgentCardProps) {
  const [isExpanded, setIsExpanded] = useState(false);

  const isRunning = status === "running";

  return (
    <div
      className={`
        rounded-lg border transition-all duration-300
        ${isRunning
          ? "animate-pulse border-neutral-700 bg-neutral-900"
          : "border-neutral-800 bg-neutral-900/50 hover:border-neutral-700"
        }
      `}
    >
      {/* Header */}
      <button
        onClick={() => setIsExpanded(!isExpanded)}
        className="flex w-full items-start gap-3 p-3 text-left"
      >
        {/* Icon container */}
        <div
          className={`
            flex h-8 w-8 shrink-0 items-center justify-center rounded-lg text-sm
            ${isRunning ? "bg-neutral-700" : "bg-neutral-800"}
          `}
        >
          {isRunning ? (
            <Loader2 className="h-4 w-4 animate-spin text-neutral-400" />
          ) : (
            <Icon className="h-4 w-4 text-neutral-400" />
          )}
        </div>

        {/* Content */}
        <div className="min-w-0 flex-1">
          <div className="flex items-center gap-2">
            <span className="text-sm font-medium text-neutral-100">{name}</span>
            {isRunning && (
              <span className="text-xs text-neutral-500">працює...</span>
            )}
            {status === "done" && (
              <span className="text-xs text-green-500">✓</span>
            )}
          </div>
          <p className="mt-1 truncate text-xs text-neutral-500">{preview}</p>
        </div>

        {/* Expand icon */}
        {isExpanded ? (
          <ChevronUp className="h-4 w-4 shrink-0 text-neutral-500" />
        ) : (
          <ChevronDown className="h-4 w-4 shrink-0 text-neutral-500" />
        )}
      </button>

      {/* Expanded content */}
      {isExpanded && (
        <div className="px-3 pb-3">
          <div className="rounded-lg border border-neutral-800 bg-neutral-950 p-3">
            <pre className="max-h-64 overflow-y-auto whitespace-pre-wrap font-mono text-xs leading-relaxed text-neutral-300">
              {content}
            </pre>
          </div>
        </div>
      )}
    </div>
  );
}
